.yaui-ctn {
  margin: 1rem;
  padding: 2rem 0.5rem 2rem 0.5rem;
}
.yaui-form {
  text-align: center;
  width: 100%;
}
.yaui-form input {
  text-align: center;
}
#ya-logo {
  width: clamp(250px, 50vw, 50%);
  height: auto;
}

/* for screens bigger than 767px */
@media only screen and (min-width: 767px) {
  html,
  body,
  #root,
  .yaui {
    height: 100%;
  }
  
  #ya-logo {
    height: auto;
  }
  .yaui-ctn {
    width: clamp(450px, 25%, 50vw);
    border-radius:10px;
    padding: 3.5rem 0.5rem 3.5rem 0.5rem;
  }
  .yaui-form {
    padding: 0 3rem 0 3rem;
  }
}
